import QrReader from 'react-web-qr-reader';
import {useState} from 'react'





export default function Scanner(){
  const delay = 500;

  const previewStyle = {
    height: 240,
    width: 320,
  };
const[cam,setCam]=useState('environment');
  const [result, setResult] = useState('No result');

  const handleScan = (result) => {
    if (result) {
      setResult(result);
    }
  };

  const handleError = (error) => {
    alert("Something went wrong try again")
  };
const changecam=()=>{
  if(cam==='environment'){
    setCam('user')
  }
  else{
    setCam('environment')
  }
}


  return (
    <>

        <div className="d-flex justify-content-center dow">
      <QrReader className="qr-image-wrapper" 
        delay={delay}
        style={previewStyle}
        onError={handleError}
        onScan={handleScan}
        facingMode={cam}
        showViewFinder={false}
      />
  </div>
  <div className="d-flex justify-content-center doyw">
  <button className='btn dwn' onClick={changecam}>Change Camera</button>
</div>
  <div className="d-flex justify-content-center dwn" style={{marginTop:'50px',opacity:'0.6'}}>
  
  <p className='text'>{result}</p>
</div>
  </>
    );
}